import { useState, useEffect, useReducer } from 'react';
import axios from 'axios';
import { activistReducer } from '../reducer/activistReducer';

const ACTIVISTS_URL = `https://example-data.draftbit.com/activists?_limit=10`;
const useActivists = () => {
  const initialState = {
    description: '',
    dateOfBirth: '',
    placeOfBirth: '',
    articleUrl: '',
    sourceUrl: '',
    imgUrl: '',
    person: '',
  };
  const [newActivist, setNewActivist] = useState(initialState);
  const [show, setShow] = useState(false);
  const [{ activists, uploading }, dispatch] = useReducer(activistReducer, {
    activists: [],
    uploading: false,
  });
  const [clap, setClap] = useState(2);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const onChange = (e) => {
    const { name, value } = e.target;
    setNewActivist({ ...newActivist, [name]: value });
  };
  const createActivist = async (file) => {
    dispatch({ type: 'UPLOADING' });
    const data = new FormData();
    data.append('upload_preset', 'xqrwjsdz');
    data.append('file', file);
    const res = await axios.post(
      `https://api.cloudinary.com/v1_1/djvzkeset/upload`,
      data,
    );
    console.log(res.data);
    if (res.data) {
      const {
        description,
        dateOfBirth,
        placeOfBirth,
        imgUrl,
        person,
      } = newActivist;
      const value = {
        description,
        dateOfBirth,
        placeOfBirth,
        imgUrl: res.data.secure_url,
        person,
      };

      try {
        const response = await axios.post(`${ACTIVISTS_URL}`, value);
        if (response.data.id) {
          setNewActivist(initialState);

          setShow(!show);

          dispatch({
            type: 'GET_ACTIVISTS',

            payload: [response.data, ...activists],
          });
        }
      } catch (err) {
        console.log(err);
      }
    }

    // const {}
  };
  useEffect(() => {
    async function getActivists() {
      try {
        const response = await axios.get(`${ACTIVISTS_URL}?_limit=10`);
        if (response.data) {
          // setActivists(response.data);
          dispatch({ type: 'GET_ACTIVISTS', payload: response.data });
        }
      } catch (err) {
        console.log(err);
      }
    }
    getActivists();
  }, []);
  const increaseClap = () => {
    // setClap(clap + 1);
  };
  const filterResults = (params) => {
    console.log('hello');
    dispatch({
      type: 'GET_ACTIVISTS',
      payload: activists.filter((a) => a.person === params),
    });
  };
  return {
    activists,
    onChange,
    newActivist,
    createActivist,
    handleShow,
    handleClose,
    show,
    clap,
    increaseClap,
    filterResults,
    uploading,
  };
};
export default useActivists;
