/* eslint-disable react/button-has-type */
import React, { useContext, useState } from 'react';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import Profile from '../../images/photo.png';
import AppContext from '../../context/AppContext';

const AddActivist = () => {
  // Create a reference to the hidden file input element
  const hiddenFileInput = React.useRef(null);
  const [displayFile, setDisplayFile] = useState(null);
  const [file, setFile] = useState(null);

  const handleClick = () => {
    hiddenFileInput.current.click();
  };
  const {
    onChange,
    newActivist,
    createActivist,
    handleClose,
    show,
    uploading,
  } = useContext(AppContext);
  console.log(uploading);

  const { description, dateOfBirth, placeOfBirth, person } = newActivist;
  const chane = (e) => {
    setDisplayFile(URL.createObjectURL(e.target.files[0]));
    setFile(e.target.files[0]);
  };
  return (
    <Modal show={show} onHide={handleClose} size="lg">
      <Modal.Header closeButton class="header">
        <Modal.Title className="add-title">
          <b>Add an activist</b>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className="p-5">
        <Form>
          <Form.Group controlId="exampleForm.ControlInput1">
            <div className="d-flex">
              <img
                className="image-modal"
                src={displayFile || Profile}
                alt="activist"
              />

              <p className="add-photo" onClick={handleClick}>
                {' '}
                Add a photo
{' '}
              </p>
              <input
                ref={hiddenFileInput}
                type="file"
                onChange={chane}
                name="imgUrl"
                style={{ display: 'none' }}
              />
            </div>
          </Form.Group>
          <Form.Group controlId="exampleForm.ControlInput1">
            <Form.Label>Activists Name</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter activist name"
              name="person"
              value={person}
              onChange={onChange}
            />
          </Form.Group>
          <Form.Group controlId="exampleForm.ControlTextarea1">
            <Form.Label>Description</Form.Label>
            <Form.Control
              as="textarea"
              rows="5"
              name="description"
              value={description}
              onChange={onChange}
            />
          </Form.Group>

          <Form.Group
            className="d-flex"
            controlId="exampleForm.ControlSelect1"
            style={{ justifyContent: 'space-between' }}
          >
            <div>
              <Form.Label>Date of Birth</Form.Label>
              <Form.Control
                type="date"
                name="dateOfBirth"
                value={dateOfBirth}
                onChange={onChange}
                placeholder="Select activist birth Date"
              />
            </div>

            <div>
              <Form.Label>Place of Birth</Form.Label>
              <Form.Control
                name="placeOfBirth"
                value={placeOfBirth}
                onChange={onChange}
                type="text"
                placeholder="Enter place of birth"
              />
            </div>
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer className="footer">
        <div onClick={() => createActivist(file)} style={{ width: '20%' }}>
          <button
            disabled={uploading}
            className="btn btn-primary activist-search"
          >
            {uploading ? 'Adding...' : 'Add Activist'}
          </button>
        </div>
      </Modal.Footer>
    </Modal>
  );
};
export default AddActivist;
