/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable react/button-has-type */
/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
import React, { useContext, useState } from 'react';
import AppContext from '../../context/AppContext';
import TileView from './TileView';
import ListView from './ListView';
import Filter from '../../images/filter.png';
import SharedButton from '../shared/SharedButton';
import List from '../../images/List.png';
import AddActivist from './AddActivist';

const ActivistsBody = () => {
  const [search, setSearch] = useState('');
  const { handleShow, filterResults } = useContext(AppContext);
  const [tile, showTile] = useState(true);
  const [list, showList] = useState(false);

  const display = () => {
    showTile(!tile);
    showList(!list);
  };

  const displayData = tile ? (
    <>
      <div className="filter">
        <img onClick={() => display()} src={Filter} alt="fileter for tile" />
      </div>
      <TileView />
    </>
  ) : (
    <>
      <div className="filter">
        <img onClick={() => display()} src={List} alt="list" />
      </div>
      <ListView />
    </>
  );
  return (
    <div className="activists-container">
      <div className="search-add">
        <div className="d-flex">
          <input
            type="text"
            placeholder="Search for activists with name, movements, etc"
            className="search"
            name="search"
            value={search}
            // onKeyPress={() => filterResults(search)}
            onChange={(e) => setSearch(e.target.value)}
          />
          <div onClick={() => filterResults(search)} style={{ width: '8rem' }}>
            <SharedButton
              classname="btn btn-primary activist-search"
              text="Search"
            />
          </div>
        </div>
        <div>
          <button onClick={handleShow} className="add">
            Add New Activist
          </button>
        </div>
      </div>

      <AddActivist />
      <>{displayData}</>
    </div>
  );
};
export default ActivistsBody;
